/*PlayerDisplay.css*/
.player-display-wrapper {
	flex-grow: 1;
}
/*Map.css*/
.map-wrapper {
	padding: 4px;
	border: 1px solid #000;
	position: relative;
	margin-top: 12px;
	flex-grow: 10;
}
.map {
	display: grid;
	font-weight: 400;
	font-size: 1rem;
}
.display-title {
	position: absolute;
    top: -8px;
    background-color: #fff;
    font-family: 'Press Start 2P', cursive;
    font-size: 0.7rem;
    left: 6px;
}
.cell {
	font-family: 'Press Start 2P', cursive;
	font-weight: 100;
}
.cell.wall::after {
	content: '#';
}
.cell.open::after {
	content: '.';
}
.cell.player::after {
	content: '@';
}
.cell.door.closed::after {
	content: '+';
}
.cell.door.open::after {
	content: '-';
}
/*HUD.css*/
.heads-up-display-log {

}
.heads-up-display-log span:nth-child(1) {
	border-right: 1px solid black;
	margin-right: 8px;
    border-right: 2px solid black;
    padding-right: 8px;
}
/*App.css*/
html, body {
	padding: 0px;
	margin: 0px;
}
.game-wrapper {
	padding: 20px;
}
.display-wrapper {
	display: flex;
	flex-direction: row;
}
.display-box {
	font-family: 'Press Start 2P', cursive;
	font-size: 0.7rem;
    padding: 4px;
    border: 1px solid black;
    margin-top: 12px;
    font-weight: 100;
	position: relative;
	margin-right: 12px;
	/* flex-grow: 1; */
}
